#page-top {
  position: fixed;
  bottom: 30px;
  right: 10px;
  font-size: 77%;
  z-index: 9;
  a {
      i {
        color:#000;
        text-decoration: none;
        width: 100px;
        text-align: center;
        display: block;
        border-radius: 5px;
        transition:all 0.6s ease;
            &:hover {
              transition:all 0.6s ease;
              text-decoration: none;
              color: chocolate;
            }
      }
  }
}
