.wrapper{

    .main-content {
      form {
        border-radius: 10px;
        border: 5px solid;
        background: antiquewhite;
        width: 450px;
        padding: 30px;
        display: block;
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        max-width: 100vw;
        max-height: 100vh;

            h3 {
              text-align: center;
              margin-bottom: 20px;
              font-size: 1.1rem;
            }

            .form-outline {
              .form-control {
                border: 1px solid;
              }
            }
      }

      .submit_page {
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        max-width: 100vw;
        max-height: 100vh;
        background: antiquewhite;
        text-align: center;
        padding: 50px;
        border: 3px solid;
        border-radius: 10px;
          a {
            color: black;
            transition: all .6s ease;
             &:hover {
               color: brown;
               transition: all .6s ease;
             }
          }
      }
    }
}
